body {
    margin: 0;
    padding: 0;
    font-family: 'Roboto', sans-serif;
    box-sizing: border-box; }

h1 {
    margin: 0 0 30px;
    font-size: 30px;
    line-height: 36px;
    text-align: center; }

.main-page {
    margin: 50px 0; }

.container {
    max-width: 1200px;
    padding: 0 15px; }

.words-field {
    display: block;
    width: 100%;
    max-width: 500px;
    margin: 0 auto 30px;
    padding: 20px;
    border: 1px solid #ccc; }

.btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100px;
    height: 40px;
    margin: 0 auto;
    cursor: pointer; }

.hide {
    display: none; }

.info-wrap {
    max-width: 500px;
    margin: 0 auto; }

.field-wrap {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin: 50px auto; }

.field {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    &.hide-colors {
        .field__item {
            background: #fff !important;
            &--active {
                background: green !important; }
            &--empty {
                background: gray !important; } } }
    &__row {
        display: flex;
        flex-wrap: wrap;
        // &:last-child
        //     .field__item
 }        //         border-bottom: 2px solid #DFE4E8

    &__item {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 51px;
        height: 51px;
        // border: 2px solid #DFE4E8
        // border: 1px solid red
        cursor: pointer;
        border-left: none;
        border-bottom: none;
        font-family: sans-serif;
        font-size: 20px;
        font-weight: 600;
        background-color: #fff;
        // &:first-child
        //     border-left: 2px solid #DFE4E8

        .borders {
            position: absolute;
            width: calc(100% + 2px);
            height: calc(100% + 2px);
            top: -1px;
            left: -1px;
            right: -1px;
            bottom: -1px;
            // box-shadow: inset 0 0 0 2px red
            // border: 2px solid #DFE4E8

            &__border {
                position: absolute;
                background-color: #000;
                z-index: 50;
                &--top {
                    width: 100%;
                    height: 2px;
                    top: 0;
                    left: 0; }
                &--right {
                    width: 2px;
                    height: 100%;
                    top: 0;
                    right: 0; }
                &--bottom {
                    width: 100%;
                    height: 2px;
                    left: 0;
                    bottom: 0; }
                &--left {
                    width: 2px;
                    height: 100%;
                    top: 0;
                    left: 0; } } }


        &--active {
            background: green !important;
            .borders {
                &__border {
                    background: red;
                    z-index: 100; } } }

        &--empty {
            font-size: 0;
            background-color: gray !important;
            .borders {
                &__border {
                    background: gray;
                    z-index: 40; } } }

        &.rm-top {
            .borders__border {
                &--top {
                    z-index: 50;
                    background-color: green; } } }
        &.rm-bottom {
            .borders__border {
                &--bottom {
                    z-index: 50;
                    background-color: green; } } }
        &.rm-left {
            .borders__border {
                &--left {
                    z-index: 50;
                    background-color: green; } } }
        &.rm-right {
            .borders__border {
                &--right {
                    z-index: 50;
                    background-color: green; } } } } }

.test {
    width: 40px;
    height: 40px;
    margin-top: 100px;
    border: 2px solid green; }

